import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  LoadingController
} from "ionic-angular";
import { HkApiproviderProvider } from "../../providers/hk-apiprovider/hk-apiprovider";

@IonicPage()
@Component({
  selector: "page-notifications",
  templateUrl: "notifications.html"
})
export class NotificationsPage {
  public resposeData: any;
  userDetails: any;

  postData = {
    user_id:"",
    token: ""
  };

  public NotidataSet: any;
  year: number;

  constructor(
    private loadingCtrl: LoadingController,
    public navCtrl: NavController,
    public navParams: NavParams,
    public auth: HkApiproviderProvider
  ) {
    this.postData.token = this.auth.gettoken();
    if(localStorage.getItem('userData')){
      const data = JSON.parse(localStorage.getItem('userData'));
      this.userDetails = data.userData;
    }
    const date = new Date();
    this.year = date.getFullYear();
    this.getField();
  }

  getField() {
    let zest = this.loadingCtrl.create({
      content: "Getting Notification..",
      duration: 20000,
      spinner: "crescent"
    });

    zest.present();
    this.auth.postData(this.postData, "getnotification").then(
      result => {
        this.resposeData = result;
        this.NotidataSet = this.resposeData.NotiData;
        console.log(this.NotidataSet);
        zest.dismiss();
      },
      err => {
        zest.dismiss();
      }
    );
  }
  openUrl(url){
    window.open(url)
    // let parseUrl:any =  this.sanitizer.bypassSecurityTrustResourceUrl(url)
    // window.open(parseUrl);
  }

  onclickHome() {
    this.navCtrl.setRoot('HomePage');
  }

  openContact() {
    this.navCtrl.push('ContactPage');
  }

  openfeedback() {
    this.navCtrl.push('FeedbackPage');
  }
  openterms() {
    this.navCtrl.push('TermsPage');
  }

  openprivacy() {
    this.navCtrl.push('TermsPage');
  }
  
  checkLogin() {
    if (localStorage.getItem('userData')) {
      return true;
    }
    return false;
  }
}



